let that:any;
import { Vue, Component } from 'vue-property-decorator'

@Component({
  components:{},
  // /**filter */
  filters: {
    dateTime(value: string){ 
      return that.getDateTime(value)
    },
    dateUnitConvert(value: string){ 
      switch(value){
        case 'MONTH':
          return '';
        case 'DAT':
          return '';
        default :
          return '';
      }
    },
  },
  directives:{
    // rainbow:{
    //   bind(el,binding,value){
    //     el.style.color = '#'+Math.random().toString(16).slice(2,8)
    //   },
    // },
  }
})
export class FormatMixin extends Vue {
  /**life hook */
  beforeCreate(){
    that = this;
  }
  /* data :  */
  // uuid: string = '123'
  /* method : methods */
  // 
  getDateTime(timeStr:string){
    if(!timeStr) return '';
    const self = new Date(timeStr);
    let format = "yyyy-MM-dd hh:mm:ss";

    /*
    * eg:format="yyyy-MM-dd hh:mm:ss";
    */ 
   let o:any = { 
       "M+" : self.getMonth() + 1, // month 
       "d+" : self.getDate(), // day 
       "h+" : self.getHours(), // hour 
       "m+" : self.getMinutes(), // minute 
       "s+" : self.getSeconds(), // second 
       "q+" : Math.floor((self.getMonth() + 3) / 3), // quarter 
       "S" : self.getMilliseconds() 
       // millisecond 
   } 
   if (/(y+)/.test(format)) { 
       format = format.replace(RegExp.$1, (self.getFullYear() + "").substr(4 
                       - RegExp.$1.length)); 
   } 
   for (var k in o) { 
       if (new RegExp("(" + k + ")").test(format)) { 
           format = format.replace(RegExp.$1, RegExp.$1.length == 1 
                           ? o[k] 
                           : ("00" + o[k]).substr(("" + o[k]).length)); 
       } 
   } 
   return format; 
  }
}



@Component
export class ValidateMixin extends Vue {
  /* data :  */
  // uuid: string = '123'
  /* method : methods */
  // 
  pwdRules(rule: any, value: string, callback: any){
    const numReg = /^[0-9]+$/;
    const letterReg = /^[a-zA-Z]+$/;
    if (numReg.test(value)) {
        callback(new Error(''))
    } else if (letterReg.test(value)) {
        callback(new Error(''))
    }
    callback()
  }
  isPwdLong(rule: any, value: string, callback: any){
    if (value.length > 20) {
      callback(new Error(''))
    }
    callback()
  }
  pwdConfirm(vm: any, selector: string = 'password'){ // 
    return (rule: any, value: string, callback: any) => {
      if (value !== vm.ruleForm[selector]) {
        callback(new Error(''))
      } else {
        callback()
      }
      
    }
  }
}


