




















const Encrypt = require('@/utils/secret.js').Encrypt;
import {Component,Vue,Prop, Mixins} from 'vue-property-decorator'
import {ValidateMixin} from '@/assets/mixin'
@Component({
  components:{}
})
export default class ViewComponent extends Mixins(ValidateMixin) {
  /* prop */
  // @Prop() prop!: any; // 

  /* data */
  ruleForm = {
    username:'admin',
    password:'admin',
  }
  rules = {
    username:[
      {message:'',trigger:'blur',required: true,},
    ],
    password : [
      {message: '',trigger: 'blur',required: true,},
      // {validator: this.isPwdLong, trigger: 'blur'},
      // {validator: this.pwdRules, trigger: 'blur'},
      // {message: '',pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/, trigger: 'blur'},
    ],
  }

  /* lifecycle hook */
  // created(){}

  /* method */
  submitForm(formName: string) { // 
    (this.$refs[formName] as any).validate(
      (valid: boolean) => {
        if (valid) {
          this.executeEmit()
        }
      }
    )
  }
  executeEmit(conf:object={}) {
    // this.$emit('update:prop',{});
    this.emit({ //  
      /*  */
      data:{
        username: this.ruleForm.username,
        password: Encrypt(this.ruleForm.password),
      }
      /*  */
    })
  }
  /*  */
  emit(response: object) {
    this.$emit(
      'emit',
      response // 
    )
  }
}
